<template>
  <div>
    <vue-confirm-dialog></vue-confirm-dialog>

    <div class="row">
      <div class="col-md-12" ref="table">
        <v-card>
          <v-card-title>
            Records

            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>

            <v-spacer></v-spacer>
          
          </v-card-title>

          <v-data-table
            
            :headers="headers"
            :items="records"
            :search="search"
            item-key="id"
          >
            <template v-slot:item.status_id="{ item }">
      
      <td class="text-right">
        <span  v-if="item.status_id==1" class="label label-lg label-light-warning label-inline"> Pendding </span>
        <span  v-if="item.status_id==2" class="label label-lg label-light-success label-inline"> Finished </span>
        </td>
    </template>
          
          </v-data-table>
        </v-card>
      </div>
    </div>

   
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";

const VueScrollTo = VueScrollTo;

export default {
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Requests"},
      { title: "AgentRequest",route: "AgentRequest" }
    ]);
  },
  data() {
    return {
      records: [],
      edit: false,
      record_id: "",
      search: "",
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        
        { text: "Service", value: "service_type.name" },
        { text: "Agent", value: "agent.agent_name" },
         { text: "Plot NO", value: "plot.plot_no" },
        { text: "Plan", value: "plan.name" },
        { text: "Client Name", value: "client_name" },
        { text: "Date", value: "created_at" },
        { text: "Actions", value: "actions", sortable: false },
        /*{ text: "Username", value: "user_master.name" },
        { text: "phone", value: "user_master.phone" },
        { text: "status", value: "status_id", sortable:false},
        */
      ],
    };
  },
getStatus(status_id){
return status_id==1?'Pendding':'Finished';
},
  created() {
    this.fetchRecords();
  },
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
 
    resetForm() {
      this.form = {
        title: null,
        description: null,
      };

      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    fetchRecords() {
      this.$ApiService
        .get("/AgentRequest")
        .then((response) => {
          console.log(response);
          this.records = response.data.data;
        })
        .catch((err) => {
            Vue.$toast.error(`${err}`)
            console.log(err);
        });
    },
    deleteItem(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to proceed?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .delete(`/Feedback/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.fetchRecords();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => Vue.$toast.error(`${err}`));
          }
        },
      });
    },
    finish() {
      this.$ApiService
        .put(`/Feedback/${this.record_id}`,this.form)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();
            this.record_id = "";
            this.$refs.table.scrollIntoView();
            this.fetchRecords();
            Vue.$toast.success(`${response.data.result}`);
            
          } else {
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => Vue.$toast.error(`${err}`));
    },
  }, // end of methods
    computed: {
    ...mapState({
      errors: state => state.auth.errors
      
    }),
   
  }
}; //end of class
</script>
